@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,300;1,400&display=swap);
*{margin:0;padding:0}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#09053a;background-attachment:fixed;color:#ddecff;font-size:18px;font-family:"Source Sans Pro", sans-serif;font-family:"Fira Code", code;scroll-behavior:smooth;overflow:auto}body a:visited,body a{color:#ddecff;text-decoration:none}body::-webkit-scrollbar{width:8px}body::-webkit-scrollbar-track{box-shadow:inset 0 0 5px white;border-radius:20px}body::-webkit-scrollbar-thumb{background:#ddecff;border-radius:10px;height:10%}body::-webkit-scrollbar-thumb:hover{background:#3cffb9}body::-webkit-scrollbar-track:hover{box-shadow:inset 0 0 10px white}#root{min-height:100vh;position:relative}.sections{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;transition:all 0.3s ease-in-out}section{width:75%;padding:6rem 0rem}section h1,section h2,section h3{font-family:"Source Sans Pro", sans-serif;color:#3cffb9}section h1{font-size:4rem;margin:1rem 0}section h2{font-size:2rem;font-weight:600;margin-bottom:3rem;text-align:center}section h3{font-size:1.35rem;font-weight:600;margin-bottom:2rem}section p{margin:1rem 0rem}@media (max-width: 500px){section{width:100%;padding:4rem 0rem}section h1{font-size:3rem}section h2{font-size:1.75rem}section h3{font-size:1.3rem}section p{font-size:1rem}.sections{padding:0rem 2rem}}

#Landing{min-height:calc(100vh - 16rem);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;position:relative}#Landing h1 span{color:#d6deff}#Landing h2{color:#ddecff;text-align:left}#Landing #cta{margin-top:3rem;justify-self:flex-end}#Landing .link-button{color:#3cffb9;display:inline-block;padding:0.6rem 1.4rem;margin-right:1rem;border:2px #3cffb9 solid;border-radius:0.3rem;transition:all 0.2s ease-in-out}#Landing .link-button:hover{color:#00ffc2;background-color:rgba(0,255,162,0.233)}#Landing #chevron{margin-top:auto;justify-self:flex-end;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;font-size:2.5rem;color:#3cffb9}#Landing #chevron svg{-webkit-animation:chevron 3s infinite ease-in alternate;animation:chevron 3s infinite ease-in alternate;margin-top:0;border-radius:1rem}#Landing #chevron svg:last-child{font-size:3rem;-webkit-animation-delay:-0.3s;animation-delay:-0.3s;margin-top:-1.8rem}@-webkit-keyframes chevron{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}50%{-webkit-transform:translate3d(0, 0.6rem, 0);transform:translate3d(0, 0.6rem, 0)}100%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}@keyframes chevron{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}50%{-webkit-transform:translate3d(0, 0.6rem, 0);transform:translate3d(0, 0.6rem, 0)}100%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}@media (max-width: 500px){#Landing{min-height:calc(100vh - 12rem)}}

nav{position:sticky;position:-webkit-sticky;top:0;font-size:1.2rem;color:#3cffb9;z-index:999}nav .nav{padding:0.75rem 2rem;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;background:rgba(255,255,255,0.2);backdrop-filter:blur(12px);-webkit-backdrop-filter:blur(15px);border:1px solid rgba(255,255,255,0.1)}nav .nav h3{display:none;width:100%;text-align:center;font-family:"Source Sans Pro", sans-serif}nav .nav span,nav .nav a:visited,nav .nav a{padding:0.3rem 0.6rem;color:#ddecff}nav .nav a:visited,nav .nav a{text-decoration:none;transition:all 0.2s ease-in-out}nav .nav a:visited:hover,nav .nav a:hover{color:#3cffb9}nav #menu{display:none}@media (max-width: 600px){nav{position:fixed;display:-webkit-flex;display:flex;right:0;width:100%}nav #menu{position:absolute;top:1.5rem;right:1.5rem;height:2.5rem;width:2.5rem;display:inline;font-size:2rem}nav #menu svg{position:absolute;top:0;left:0;transition:all 0.5s ease-in-out;background:#464696;padding:0.25rem;border-radius:5px}nav #menu svg.false{-webkit-clip-path:circle(0% at 50% 50%);clip-path:circle(0% at 50% 50%)}nav #menu svg.true{-webkit-clip-path:circle(150% at 50% 50%);clip-path:circle(150% at 50% 50%)}nav .nav{height:auto;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;background:#0b0b27;transition:all 0.5s ease-in-out;width:100%}nav .nav h3{display:block}nav .nav.false{-webkit-clip-path:circle(0% at 100% 0);clip-path:circle(0% at 100% 0);display:none}nav .nav.true{-webkit-clip-path:circle(150% at 100% 0);clip-path:circle(150% at 100% 0)}nav .nav.flex-true{display:-webkit-flex;display:flex}nav .nav a{margin-left:2rem}nav.true+.sections{-webkit-filter:opacity(0.2);filter:opacity(0.2)}nav.false+.sections{-webkit-filter:opacity(1);filter:opacity(1)}}

#About{max-width:70ch}#About #skills{margin-top:3rem;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}#About #skills div{margin-top:2rem;margin-right:8rem}

.Project{margin:auto;display:grid;grid-template-columns:45% 55%;margin-bottom:5rem;margin-top:5rem}.Project .project-preview{border-radius:0.75rem;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center}.Project .project-preview .image{display:-webkit-inline-flex;display:inline-flex;position:relative;border-radius:0.75rem;box-shadow:0.15rem 0.15rem 0 2px #00ffc2;transition:all 0.3s ease-in-out}.Project .project-preview .image img{-webkit-transform:translate(-0.5rem, -0.5rem);transform:translate(-0.5rem, -0.5rem);transition:all 0.3s ease-in-out;border-radius:0.75rem;max-width:100%;-webkit-filter:brightness(0.75);filter:brightness(0.75)}.Project:hover .project-preview .image{box-shadow:0.1rem 0.1rem 0 1px #3cffb9}.Project:hover .project-preview .image img{-webkit-filter:brightness(1);filter:brightness(1);-webkit-transform:translate(0, 0);transform:translate(0, 0)}.Project .project-info{margin:auto;margin-left:0;margin-right:0;min-height:12rem;max-width:60ch;border-radius:0.75rem;border-top-left-radius:0;border-bottom-left-radius:0;padding:0.75rem 1rem;padding-left:2rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background:#1e1f61;-webkit-justify-content:space-evenly;justify-content:space-evenly}.Project .project-info h3{margin-bottom:0;font-family:"Fira Code", code;color:unset;font-weight:500}.Project .project-info .title{text-align:center;font-size:1.4rem;margin-bottom:1rem}.Project .project-info .title span{color:#3cffb9}.Project .project-info .description{position:relative;padding-right:0.5rem;font-size:1.1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between}.Project .project-info .description .project-meta{font-size:1rem;font-weight:300;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-flex-wrap:wrap;flex-wrap:wrap}.Project .project-info .description .project-meta div{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;margin-top:1rem}.Project .project-info .description .project-meta span{margin-right:1rem;color:#3cffb9}.Project .project-info .description .project-meta span.--link{color:#ddecff;margin:0}.Project .project-info .description .project-meta span.--link i{position:relative;margin-right:3ch}.Project .project-info .description .project-meta span.--link i svg{position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.Project .project-info .description .project-meta span.--link:hover{color:#3cffb9}#Projects :nth-child(odd) .Project{grid-template-columns:none;grid-template-columns:55% 45%}#Projects :nth-child(odd) .Project .project-preview{-webkit-order:2;order:2;-webkit-justify-content:flex-start;justify-content:flex-start}#Projects :nth-child(odd) .Project .project-info{-webkit-order:1;order:1;margin-left:auto;padding:0.75rem 1rem;padding-left:2rem;border-radius:0.75rem;border-top-right-radius:0;border-bottom-right-radius:0}@media (max-width: 900px){#Projects .Project{grid-template-columns:100%}#Projects .Project .project-preview{-webkit-justify-content:center;justify-content:center}#Projects .Project .project-preview .image{box-shadow:0 0 0 2px #00ffc2}#Projects .Project .project-preview .image img{-webkit-transform:none;transform:none}#Projects .Project .project-info{border-radius:0.75rem;padding:0.75rem 1rem;margin-top:-5rem;z-index:2}#Projects :nth-child(odd) .Project{grid-template-columns:100%}#Projects :nth-child(odd) .Project .project-preview{-webkit-order:1;order:1;-webkit-justify-content:center;justify-content:center}#Projects :nth-child(odd) .Project .project-info{-webkit-order:2;order:2;border-radius:0.75rem;padding:0.75rem 1rem}}

#sidelinks{position:fixed;right:0;top:calc(50vh + 2rem);margin-right:1.5rem;-webkit-transform:translateY(-50%);transform:translateY(-50%)}#sidelinks .container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}#sidelinks .container a:visited,#sidelinks .container a{transition:all 0.2s ease-in-out;color:#ddecff;text-decoration:none;padding:0.5rem 0;font-size:1.5rem;position:relative}#sidelinks .container a:visited span,#sidelinks .container a span{transition:all 0.2s ease-in-out;width:8ch;text-align:center;border-radius:0.25rem;color:#3cffb9;font-size:1rem;padding:0.5rem;display:none;position:absolute;right:3rem;top:50%;-webkit-transform:translateY(-55%);transform:translateY(-55%);background:#030e38;box-shadow:1px 1px 2px #3cffb9}#sidelinks .container a:visited:hover,#sidelinks .container a:hover{color:#3cffb9}#sidelinks .container a:visited:hover span,#sidelinks .container a:hover span{opacity:1;display:inline}#sidelinks .container::before,#sidelinks .container::after{content:"";width:0;border:solid #ddecff 1.5px;background-color:#ddecff;position:fixed;height:calc((80vh / 2) - 4rem);border-radius:2rem;right:50%}#sidelinks .container::before{top:0;-webkit-transform:translate(50%, -105%);transform:translate(50%, -105%)}#sidelinks .container::after{-webkit-transform:translate(50%, 105%);transform:translate(50%, 105%);bottom:0}@media (max-width: 768px){#sidelinks{display:none}}

